<template>
    <div>
        <PageTitle :title="'Gacha'" />
        <md-card>
            <md-card-header>
                <md-card-header-text>
                <div class="md-title">{{title}}</div>
                <div class="md-subhead">{{condition}}</div>
                </md-card-header-text>

                <md-card-media>
                    <img class="cardBackImage" :src="src">
                </md-card-media>
            </md-card-header>

            <md-card-actions>
                <md-button @click="doGacha">Draw Gacha</md-button>
            </md-card-actions>
        </md-card>
    </div>
</template>

<script>
import { mapActions } from 'vuex'

export default {
    name:'gachaButton',
    data:() => ({
        isLoading:false
    }),
    props:['title', 'condition', 'src'],
    methods:{
        ...mapActions(['showLoading', 'hideLoading']),
        doGacha (){
            this.showLoading();

            setTimeout(()=>{this.hideLoading();}, 1000);
        }
    }
}
</script>

<style scoped>
#loader{
    display:none;
}
.md-card{
    margin-bottom: 2%;
}
.cardBackImage{
    width:70%;
}
.md-card-media{
    text-align:center;
}
</style>
