<template>
  <div class="page-container">
    <Theme :darkTheme="darkTheme" />
    <md-app md-mode="reveal">
      <md-app-toolbar class="titleBar">
        <md-button class="md-icon-button" @click="menuVisible = !menuVisible">
          <md-icon>menu</md-icon>
        </md-button>
        <span class="md-title">GACHA MONSTER</span>
      </md-app-toolbar>

      <md-app-drawer :md-active.sync="menuVisible">
        <md-toolbar class="md-transparent" md-elevation="0">
          <Login :username="username"/>
        </md-toolbar>

        <md-list>
          <md-list-item>
            <md-icon>view_module</md-icon>
            <span class="md-list-item-text" @click="menuVisible = !menuVisible">
              <router-link to="/" class="py-1 px-4 d-block">
                HOME
              </router-link>
            </span>
          </md-list-item>

          <md-list-item>
            <md-icon>book</md-icon>
            <span class="md-list-item-text" @click="menuVisible = !menuVisible">
              <router-link to="/myCard" class="py-1 px-4 d-block">
                SELL
              </router-link>
            </span>
          </md-list-item>

          <md-list-item>
            <md-icon>search</md-icon>
            <span class="md-list-item-text" @click="menuVisible = !menuVisible">
              <router-link to="/cardPool" class="py-1 px-4 d-block">
                POOL
              </router-link>
            </span>
          </md-list-item>

          <md-list-item>
            <md-icon>sync</md-icon>
            <span class="md-list-item-text" @click="menuVisible = !menuVisible">
              <router-link to="/gacha" class="py-1 px-4 d-block">
                DRAW
              </router-link>
            </span>
          </md-list-item>

          <md-list-item>
            <md-icon>view_module</md-icon>
            <span class="md-list-item-text" @click="menuVisible = !menuVisible">
              <router-link to="/history">
                HISTORY
              </router-link>
            </span>
          </md-list-item>

          <md-list-item>
            <md-icon>view_module</md-icon>
            <span class="md-list-item-text" @click="changeTheme">
              <div class="py-1 px-4 d-block">
                Theme
              </div>
            </span>
          </md-list-item>
          <md-list-item>
            <md-icon>view_module</md-icon>
            <span class="md-list-item-text" @click="changeLang('en')">
              <div class="py-1 px-4 d-block">
                Language : English
              </div>
            </span>
          </md-list-item>
          <md-list-item>
            <md-icon>view_module</md-icon>
            <span class="md-list-item-text" @click="changeLang('ko')">
              <div class="py-1 px-4 d-block">
                Language : 한국어
              </div>
            </span>
          </md-list-item>
        </md-list>
      </md-app-drawer>
      <md-app-content>
        <router-view/>
        <Loading v-if="showLoading" />
      </md-app-content>
    </md-app>
  </div>
</template>

<script>
import sc from './js/steemconnect.js'
import Login from './components/SteemConnectLogin.vue'
import CONFIG from '../config.json'
import Theme from './components/Theme.vue'
import Loading from './components/Loading.vue'

export default {
  name: 'GachaMonster',
  data: () => ({
    menuVisible: false,
    color:'orange',
    size:'10',
    darkTheme:true
  }),
  computed: {
    username:  () => {
      return localStorage.getItem(CONFIG.STEEMCONNECT.STORAGE_ACCESS_TOKEN) ? localStorage.getItem(CONFIG.STEEMCONNECT.USERNAME) : '';
    },
    showLoading() {
      return this.$store.state.ui.showLoading;
    },
  },
  components:{
    Login,
    Theme,
    Loading
  },
  created:async function() {
    this.darkTheme = JSON.parse(localStorage.getItem(CONFIG.SM_GACHA.STORAGE_THEME));
  },
  methods:{
    changeTheme(){
      this.darkTheme = !this.darkTheme;
      this.menuVisible = !this.menuVisible;
      localStorage.setItem(CONFIG.SM_GACHA.STORAGE_THEME, this.darkTheme)
    },
    changeLang(locale){
      localStorage.setItem(CONFIG.SM_GACHA.LOCALE, locale);
      location.href = '/';
    }
  }
}
</script>

<style lang="scss" scoped>
    .page-container{
      height:100%;
    }

  .md-app {
    // max-height: 600px;
    border: 1px solid rgba(#000, .12);
  }

   // Demo purposes only
  .md-drawer {
    width: 230px;
    max-width: calc(100vw - 125px);
  }

  .md-content{
      min-height: 640px;
  }

  .md-title{
    font-size:1.8rem;
  }
 
.loader{
    width: 100%;
    height: 100%;
    text-align: center;
    position: absolute;
    // background-color: rgb(0, 0, 88, 0.5);
    left: 0px;
    top: 0px;
    z-index: 9;
}
</style>
