<template>
    <div>
        <img :name="cardName" :src="imgSrc()" class="card-img">
    </div>
</template>

<script>
export default {
    name:'CardImage',
    props:['cardName', 'edition', 'gold'],
    methods:{
      imgSrc:function() {
        return `https://steemitimages.com/160x0/https://s3.amazonaws.com/steemmonsters/${this.edition == 0 ? 'cards_v2.2' : 'cards_beta'}/${this.cardName}${this.gold ? '_gold' : ''}.png`;
      }
    }
}
</script>

