import { Client } from 'steemconnect';
import CONFIG from '../../config.json'

let callbackUrl = '';

console.log('ENV:' + process.env.NODE_ENV);

if (process.env.NODE_ENV == 'development') {
    callbackUrl = CONFIG.STEEMCONNECT.CALLBACKURL_DEV;
} else {
    callbackUrl = CONFIG.STEEMCONNECT.CALLBACKURL_PROD;
}

const client = new Client({
    app: CONFIG.STEEMCONNECT.APP,
    callbackURL: callbackUrl,
    scope: CONFIG.STEEMCONNECT.SCOPE
});

client.customJson = (id, account, payload, cb) => {
    client.setAccessToken(localStorage.getItem(CONFIG.STEEMCONNECT.STORAGE_ACCESS_TOKEN));
    client.broadcast(
        [
            [
                'custom_json',
                {
                    id: id,
                    required_auths: [],
                    required_posting_auths: [account],
                    json: JSON.stringify(payload),
                },
            ],
        ],
        cb,
    );
};

export default client;