<template> 
    <div>
        
        <div>
            <canvas id="canvas" style="position:absolute;margin:0;padding:0;width:100%;height:100%;top:100px;"></canvas>
        </div>

        <input type="button"  @click="loadflr" value="test" />
    </div>
</template>
    

<script>

export default {
    name:'example',
    methods:{
        loadflr:function(){
            debugger;
            let flareExample;
            flareExample = new FlareExample(document.getElementById("canvas"), function () {
            flareExample.load("./Jacko.flr", function (error) {
                if (error) {
                    console.log("failed to load actor file...", error);
                }
            });

            flareExample.setSize(684, 387);
            document.body.addEventListener('dragover', function (evt) {
                evt.stopPropagation();
                evt.preventDefault();
                evt.dataTransfer.dropEffect = "copy";
            }, true);

            document.body.addEventListener('dragleave', function (evt) {
                evt.stopPropagation();
                evt.preventDefault();
            });

            document.body.addEventListener("drop", function (evt) {
                // Reload another actor by dragging and dropping the file in.
                evt.stopPropagation();
                evt.preventDefault();

                const files = evt.dataTransfer.files;

                flareExample.load(files[0], function (error) {
                    if (error) {
                        console.log("oh no", error);
                    }
                });
            }, true);
        });
        }
    }
}
</script>

