<script>
import { mapActions } from 'vuex';

export default{
    name:'Main',
    data(){
        return {
            show1:false,
            show2:true
        }
    },
    methods: {
         ...mapActions(['showLoading', 'hideLoading']),
         goPage(page){
             location.href = `/${page}`;
         },
         testAnimation(){
             this.show2 = !this.show2;
             setTimeout(() => {this.show1 = !this.show1;}, 1000);
         }
     }
}
