<template>
    <div>
        <md-button v-if="!username" class="md-dense md-raised md-primary" @click="login">Log In</md-button>
        <md-avatar v-if="username">
        <img :src="accountImage" alt="Avatar">
        </md-avatar>
        <md-button v-if="username" class="md-accent" @click="logout">Log Out</md-button>
    </div>
</template>

<script>
import sc from '../js/steemconnect.js'
import CONFIG from '../../config.json'

export default {
    name: 'Login',
    props:[
        'username'
    ],
    computed:{
        accountImage:function() {
            return `https://steemitimages.com/u/${this.username}/avatar`;
        }
    },
    methods: {
        login() {
            sc.login({}, (err, token) => {
                window.location = '/';
            });
        },
        logout(){
            localStorage.removeItem(CONFIG.STEEMCONNECT.STORAGE_ACCESS_TOKEN);
            localStorage.removeItem(CONFIG.STEEMCONNECT.USERNAME);
            localStorage.removeItem(CONFIG.STEEMCONNECT.EXPIRES_IN);
            window.location = '/';
        },
    }
}
</script>

<style lang="scss" scoped>
  small {
    display: block;
  }

  #account {
      display:inline-block;
  }
</style>