<template>
    <div>
        <PageTitle :title="`Card Pool`" />
        <CardFilter :cards="cards" 
            :cardType="cardType"
            :rarity="rarity"
            :element="element"
            :unitType="unitType"
            @sortClick="sortClickEvent" />
        <br>
        <br>
        <div v-if="msg.length > 0" style="text-align:center;"><h1>{{msg}}</h1></div>
        <div class="cardList" v-for="card in cards">
            <CardImage :cardName="card.name" :gold="card.isGold" :edition="card.edition" /> 
        </div>
    </div>
</template>

<script>
import CONFIG from '../../config.json'
import CardImage from './CardImage.vue';
import Card from './Card.vue'
import CardFilter from './CardFilter.vue'
import PageTitle from './Title.vue'

const SPLIT_CHAR = '-';

export default {
    name: 'CardPool',
    components:{
        Card,
        CardFilter,
        PageTitle,
        CardImage
    },
    data:() => ({
        msg:'',
        maxSendCount:50,
        tokenUnit:CONFIG.SM_GACHA.TOKEN_UNIT,
        cardType:CONFIG.CARD_INFO.FOIL,
        rarity:CONFIG.CARD_INFO.RARITY,
        element:CONFIG.CARD_INFO.ELEMENT,
        unitType:CONFIG.CARD_INFO.UNITTYPE,
        userCards:[],
        allCards:[],
        cardsPrice:[],
        cards:[]
    }),
    created: async function() {
        let userName = localStorage.getItem('username');
        // 커먼 200 레어 400 에픽 800 레전 2000
        if (process.env.NODE_ENV == 'development') {
            userName = 'smseller';
            // userName = 'happyberrysboy';
        }

        if(!userName) {
            this.msg = 'Please Login';
            return;
        }

        // Get User Card List
        await fetch(CONFIG.STEEMMONSTERS_API.USER_CARDS + userName)
            .then(res => res.json())
            .then(function(json){
                this.userCards = json.cards;
            }.bind(this));

        // Get All Steem Monsters Cards
        await fetch(CONFIG.STEEMMONSTERS_API.ALL_CARDS)
            .then(res => res.json())
            .then(function(json){
                this.allCards = json;
            }.bind(this));

        // Get All Market Cards
        await fetch(CONFIG.STEEMMONSTERS_API.CARD_PRICE)
            .then(res => res.json())
            .then(function(json){
                this.cardsPrice = json;
            }.bind(this));

        // 카드종류별로 수량을 샌다(cardid, gold, edition 으로 구분)
        let cardsCount = await this.userCards.reduce((acc, curr) => {
            let key = curr.card_detail_id + SPLIT_CHAR + curr.gold + SPLIT_CHAR + curr.edition;

            if (acc.hasOwnProperty(key)) {
                acc[key] = acc[key] + 1;
            } else {
                acc[key] = 1;
            }

            return acc;
        }, {});

        // Card.vue에 설정될 값들을 할당
        Object.keys(cardsCount).forEach((key) => {
            let [cardId, isGold, cardEdition] = key.split(SPLIT_CHAR);
            isGold = JSON.parse(isGold);

            //  User Card 중에 조건에 맞는 카드를 가져온다.
            let tmpUserCards = this.userCards.filter(
                function(data){
                    return data.card_detail_id == cardId 
                    && data.gold == isGold
                    && data.edition == cardEdition
                }
            )[0];

            // All Card 중에 조건에 맞는 카드를 가져온다.
            let tmpCardInfo = this.allCards.filter(
                    function(data){return data.id == cardId}
                )[0];

            // Price Card 중에 조건에 맞는 카드를 가져온다.
            let cardPrice = this.cardsPrice.filter(
                function(data){
                    return data.card_detail_id == cardId 
                    && data.gold == isGold
                    && data.edition == cardEdition
                }
            );

            // 카드가 1장 이상 결합된 카드들은 모두 거른다.
            if(isGold){
                // Gold는 기본 경험치들이 존재하므로 수치 이상이면 제외
                if(tmpCardInfo.rarity === 1 && tmpUserCards.xp > CONFIG.CARD_INFO.GOLD_COMMON_XP) return;
                if(tmpCardInfo.rarity === 2 && tmpUserCards.xp > CONFIG.CARD_INFO.GOLD_RARE_XP) return;
                if(tmpCardInfo.rarity === 3 && tmpUserCards.xp > CONFIG.CARD_INFO.GOLD_EPIC_XP) return;
                if(tmpCardInfo.rarity === 4 && tmpUserCards.xp > CONFIG.CARD_INFO.GOLD_LEGENDARY_XP) return;
            }else{
                if(tmpUserCards.xp > 0) return;
            }

            // Card.vue에 사용될 값들을 할당
            this.cards.push({
                "id":tmpCardInfo.id,
                "name":tmpCardInfo.name,
                "type":tmpCardInfo.type,
                "rarity":tmpCardInfo.rarity,
                "edition":cardEdition,
                "isGold":isGold,
                "color":tmpCardInfo.color,
                "market_id":tmpUserCards.market_id,
                "price":cardPrice.length == 0 ? 0 : Math.floor(cardPrice[0].low_price * 100) / 100,
                "count":cardsCount[key],
                "selectedCount":0,
                "marketCount":0
            });
        });
    },
    methods:{
        sortClickEvent:function(asc){
            if(asc == 'Asc'){
                this.cards.sort(function (a, b){
                    return a.price < b.price ? -1 : a.price > b.price ? 1 : 0;
                });
            }else{
                this.cards.sort(function (a, b){
                    return a.price < b.price ? 1 : a.price > b.price ? -1 : 0;
                });
            }
        }
    }
}

</script>

<style scoped>
.myCard{
    margin-top:-10px;
}
.cardList {
    display:inline-block;
}
</style>