<template>
    <div>
        <div style="width:100%;display:inline-flex;">
            <div class="box" style="width:25%;">
                <div class="title">
                    FOIL
                </div>
                <div>
                    <span @click="selectFoil(0)" :class="foilToggleClass(0)"><md-icon class="icon" style="color:#000;">sd_card</md-icon></span>
                    <span @click="selectFoil(1)" :class="foilToggleClass(1)"><md-icon class="icon" style="color:rgb(243, 156, 18);">sd_card</md-icon></span>
                </div>
            </div>
            <div class="box" style="width:50%;">
                <div class="title">
                    RARITY
                </div>
                <div>
                    <span @click="selectRarity(1)" :class="rarityToggleClass(1)"><md-icon class="icon" style="color:rgb(123, 123, 123);">brightness_1</md-icon></span>
                    <span @click="selectRarity(2)" :class="rarityToggleClass(2)"><md-icon class="icon" style="color:rgb(52, 152, 219);">brightness_1</md-icon></span>
                    <span @click="selectRarity(3)" :class="rarityToggleClass(3)"><md-icon class="icon" style="color:rgb(142, 68, 173);">brightness_1</md-icon></span>
                    <span @click="selectRarity(4)" :class="rarityToggleClass(4)"><md-icon class="icon" style="color:rgb(243, 156, 18);">brightness_1</md-icon></span>
                </div>
            </div>
            <div class="box" style="width:25%;">
                <div class="title">
                    PRICE
                </div>
                <div>
                    <span @click="sort('Asc')"><md-icon class="icon" style="color:#000;">keyboard_arrow_up</md-icon></span>
                    <span @click="sort('Desc')"><md-icon class="icon" style="color:#000;">keyboard_arrow_down</md-icon></span>
                </div>
            </div>
        </div>
            <div style="width:100%;display:inline-flex;margin-top:10px;">
            <div class="box" style="width:75%;">
                <div class="title">
                    ELEMENT
                </div>
                <div>
                    <!--beenhere-->
                    <span @click="selectElement('Red')" :class="elementToggleClass('Red')"><md-icon class="icon" style="color:#f00;">whatshot</md-icon></span>  
                    <span @click="selectElement('Blue')" :class="elementToggleClass('Blue')"><md-icon class="icon" style="color:#00f;">waves</md-icon></span>
                    <span @click="selectElement('Green')" :class="elementToggleClass('Green')"><md-icon class="icon" style="color:#0f0;">local_florist</md-icon></span>
                    <span @click="selectElement('White')" :class="elementToggleClass('White')"><md-icon class="icon" style="color:#bbb;">flare</md-icon></span>
                    <span @click="selectElement('Black')" :class="elementToggleClass('Black')"><md-icon class="icon" style="color:#000;">brightness_7</md-icon></span>
                    <span @click="selectElement('Gold')" :class="elementToggleClass('Gold')"><md-icon class="icon" style="color:rgb(243, 156, 18);">album</md-icon></span>
                    <span @click="selectElement('Gray')" :class="elementToggleClass('Gray')"><md-icon class="icon" style="color:#555;">beenhere</md-icon></span>
                </div>
            </div>
            <div class="box" style="width:25%;">
                <div class="title">
                    TYPE
                </div>
                <div>
                    <span @click="selectUnitType('Monster')" :class="unitTypeToggleClass('Monster')"><md-icon class="icon" style="color:#000;">pets</md-icon></span>
                    <span @click="selectUnitType('Summoner')" :class="unitTypeToggleClass('Summoner')"><md-icon class="icon" style="color:#000;">security</md-icon></span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    name : 'cardFilter',
    data:() => ({
    }),
    props:['cards', 'cardType', 'rarity', 'element', 'unitType'],
    methods:{
        foilToggleClass(tag){
            const isSelected = !this.cardType.includes(tag);
            return {'inactive' : isSelected}
        },
        rarityToggleClass(tag){
            const isSelected = !this.rarity.includes(tag);
            return {'inactive' : isSelected}
        },
        elementToggleClass(tag){
            const isSelected = !this.element.includes(tag);
            return {'inactive' : isSelected}
        },
        unitTypeToggleClass(tag){
            const isSelected = !this.unitType.includes(tag);
            return {'inactive' : isSelected}
        },
        selectFoil(type){
            if(this.cardType.includes(type)) {
                this.cardType.splice(this.cardType.indexOf(type), 1);
            }else {
                this.cardType.push(type);
            }
        },
        selectRarity(rarity){
            if(this.rarity.includes(rarity)) {
                this.rarity.splice(this.rarity.indexOf(rarity), 1);
            }else {
                this.rarity.push(rarity);
            }
        },
        selectElement(element){
            console.log(element);
            if(this.element.includes(element)) {
                this.element.splice(this.element.indexOf(element), 1);
            }else {
                this.element.push(element);
            }
        },
        selectUnitType(unitType){
            console.log(unitType);
            if(this.unitType.includes(unitType)) {
                this.unitType.splice(this.unitType.indexOf(unitType), 1);
            }else {
                this.unitType.push(unitType);
            }
        },
        sort:function(asc){
            this.$emit('sortClick', asc);
        }
    }
}
</script>

<style scoped>
    .box {
        text-align:center;
    }
    .title {
        font-weight:bold;
        font-size:1rem;
        margin-bottom:10px;
        text-align:center;
    }
    .icon {
        font-size:2rem!important;
        margin:0 3px 0 3px;
    }

    .inactive{
        opacity:0.3;
    }
</style>