<template>
    <div>
    </div>
</template>

<script>
import CONFIG from '../../config.json'

export default {
    props:['accessToken', 'username', 'expiresIn'],
    created() {
        if(!this.accessToken){
            alert('Login Fail');
            localStorage.removeItem(CONFIG.STEEMCONNECT.STORAGE_ACCESS_TOKEN, this.accessToken);
            localStorage.removeItem(CONFIG.STEEMCONNECT.USERNAME, this.username);
            localStorage.removeItem(CONFIG.STEEMCONNECT.EXPIRES_IN, this.expiresIn);
            window.location = '/';
            return;
        }
        
        localStorage.setItem(CONFIG.STEEMCONNECT.STORAGE_ACCESS_TOKEN, this.accessToken);
        localStorage.setItem(CONFIG.STEEMCONNECT.USERNAME, this.username);
        localStorage.setItem(CONFIG.STEEMCONNECT.EXPIRES_IN, this.expiresIn);

        window.location = '/';
    }
}
</script>
