import anime from 'animejs/lib/anime.es.js'

const animate = anime;

animate.count = (targetQuerySelectors, fromValue, toValue, round = 1, easing = 'easeInOutExpo') => {
    return {
        targets: targetQuerySelectors,
        value: [fromValue, toValue],
        round: round,
        easing: easing
    };
}

export default animate;