<template>
  <div v-if="unitType.includes(card.type) && element.includes(card.color) && rarity.includes(card.rarity)">
    <div v-if="card.isGold ? cardType.includes(1) : cardType.includes(0)">
      <div class="cardArea">
        <div class="card" :data-gold="card.isGold" :data-id="card.id" :data-market="card.market_id">
          <CardImage :cardName="card.name" :gold="card.isGold" :edition="card.edition" />
          <div class="label name">{{card.name}}</div>
          <div class="label price">${{card.price}}</div>
          <!-- <div class="label getToken" v-show="amountToken > 0">{{amountToken.toFixed(0)}} {{tokenUnit}}</div> -->
          <div class="label getToken"></div>
          <div class="label count">
            <span @click="minusCount"><md-icon class="icon" style="margin-right:10px;">remove_circle_outline</md-icon></span>
            {{card.selectedCount}}/{{card.count}}
            <span @click="plusCount"><md-icon class="icon" style="margin-left:10px;" @click="plusCount(card)">add_circle_outline</md-icon></span>
          </div>
          <div class="exchangeArea">
            <md-dialog-confirm
              :md-active.sync="activeDialog"
              md-title="Card Exchange"
              :md-content="message"
              md-confirm-text="Agree"
              md-cancel-text="Disagree"
              @md-cancel="onCancel"
              @md-confirm="onConfirm" />
            <md-button class="md-primary md-raised btnExchange" @click="validConfirm(card)">Exchange</md-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CONFIG from '../../config.json'
import sc from '../js/steemconnect.js'
import CardImage from './CardImage.vue'
import { mapActions } from 'vuex'
import anime from 'animejs/lib/anime.es.js'

export default {
    name:'card',
    data: () => ({
      // selectedCount:0,
      tokenUnit:CONFIG.SM_GACHA.TOKEN_UNIT,
      activeDialog: false
    }),
    props:['card', 'cardType', 'rarity', 'element', 'unitType'],
    components:{
      CardImage
    },
    computed:{
      amountToken: function(){return this.card.selectedCount * this.card.price * 100},
      message: function(){return `Are you sure to exchange ${this.card.name}(Amount:${this.card.selectedCount}) card${this.card.selectedCount > 1 ? 's' : ''} and ${this.amountToken.toFixed(0)} ${this.tokenUnit}?`;}
    },
    methods:{
      ...mapActions(['showLoading', 'hideLoading']),
      async animateCount(fromVal, toVal){
        const ele = this.$el.querySelector('.getToken');

        if(!ele) return;
      
        const val = {
          amountToken:fromVal * this.card.price * 100,
          tokenUnit:CONFIG.SM_GACHA.TOKEN_UNIT
        };

        const self = this;

        anime({
          targets: val,
          amountToken: toVal * this.card.price * 100,
          round: 1,
          easing: 'linear',
          update(){
            if(self.card.selectedCount){
              ele.innerHTML = `${val.amountToken} ${val.tokenUnit}`;
            }else{
              ele.innerHTML = `${val.amountToken} ${val.tokenUnit}`;
              setTimeout(() => {ele.innerHTML = '';}, 1000);
            }
          }
        });
      },
      minusCount: function(){
        const preVal = this.card.selectedCount;

        this.card.selectedCount = this.card.selectedCount == 0 ? 0 : this.card.selectedCount - 1;
        if(preVal != 0){
          this.animateCount(this.card.selectedCount + 1, this.card.selectedCount);
        }
      },
      plusCount: function(){
        const preVal = this.card.selectedCount;

        if(this.card.selectedCount == CONFIG.SM_GACHA.MAX_EXCHANGE_COUNT){
          alert(`The maximum quantity you can sell is ${CONFIG.SM_GACHA.MAX_EXCHANGE_COUNT} cards.`);
          return;
        }
        this.card.selectedCount = this.card.selectedCount == this.card.count ? this.card.selectedCount : this.card.selectedCount + 1;
        if(preVal != this.card.count){
          this.animateCount(this.card.selectedCount - 1, this.card.selectedCount);
        }
      },
      validConfirm:function(){
        if(this.card.selectedCount == 0) return;
        this.activeDialog = true;
      },
      onCancel:function(){
        // alert('cancel');
      },
      onConfirm:function(){
        this.showLoading();
        const self = this;

        sc.customJson(
          'SellCard',
          localStorage.getItem(CONFIG.STEEMCONNECT.USERNAME),
          {
            cardname:this.card.name, 
            amount:this.card.selectedCount, 
            amountToken:this.amountToken.toFixed(0), 
            tokenUnit:this.tokenUnit
          },
          function(err, result) {
            self.hideLoading();
            if(err){
              console.log(err);
              return;
            }
            console.log(result);
          }
        );
      }
    }
}
</script>

<style scoped>
.card{
  margin:5px;
  position:relative;
  z-index:99;
}
.card.selected{
  margin:2px;
}
.label{
  font-size:0.9rem;
  font-weight:bold;
  color:#fff;
  text-align:center;
  text-shadow: 0 0 3px #000;
}
.name{
  position:absolute;
  text-align:center;
  width:100%;
  top:152px;
}
.price{
  position:absolute;
  text-align:right;
  width:100%;
  top:10px;
  padding-right:13px;
}
.getToken{
  position:absolute;
  font-size:1.5rem;
  text-align:center;
  width:100%;
  top:96px;
  color:#fff;
  text-align:right;
  padding-right:15px;
}
.count{
  position:absolute;
  text-align:center;
  width:100%;
  top:183px;
}
.icon{
  font-weight:100;
  font-size:2rem!important;
  color:#fff!important;
}
.exchangeArea{
  font-weight:bold;
  font-size:1rem;
}
.btnExchange{
  margin:0px 0px 20px 0px;
  width:100%;
}
</style>